import Vue from 'vue'
import Router from 'vue-router'
import Cookies from 'js-cookie'

import { routes } from '@/constants'

Vue.use(Router)

const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  // 未登录状态直接退出
  if (Cookies.get('isLogin')) {
    const isLogin = Cookies.get('isLogin')
    if (!isLogin) {
      router.push('/login')
    }
  } else {
    if (to.path !== '/login') {
      router.push('/login')
    }
  }
  // 根据菜单权限鉴权
  if (Cookies.get('menuList')) {
    const menuList = JSON.parse(Cookies.get('menuList'))
    const allPath = [...menuList.map(v => v.path), ...router.options.routes.map(v => v.path)]
    if (!allPath.includes(to.path)) {
      router.push('/login')
    }
  } else {
    const allPath = router.options.routes
      .find(v => v.name === 'Home').children
      .map(v => v.path)
    if (allPath.includes(to.path)) {
      router.push('/login')
    }
  }
  next()
})

export default router
