/**
 * 获取有权限的菜单
 * @param {*} menu
 */
const getAuthorizedMenu = menu => {
  const children = siteRoutes.filter(route => {
    return menu.includes(route.meta.name) || route.meta.isChildren
  })

  return children.map(v => {
    return {
      path: v.path,
      name: v.meta.name,
      isChildren: v.meta.isChildren
    }
  })
}

export {
  routes,
  getAuthorizedMenu
}
